module.exports = "import { Component, ChangeDetectionStrategy, OnInit } from '@angular/core';\nimport {\n  getMonth,\n  startOfMonth,\n  startOfWeek,\n  startOfDay,\n  endOfMonth,\n  endOfWeek,\n  endOfDay\n} from 'date-fns';\nimport RRule from 'rrule';\nimport { CalendarEvent } from 'angular-calendar';\nimport { colors } from '../demo-utils/colors';\n\ninterface RecurringEvent {\n  title: string;\n  color: any;\n  rrule?: {\n    freq: RRule.Frequency;\n    bymonth?: number;\n    bymonthday?: number;\n    byweekday?: RRule.Weekday[];\n  };\n}\n\n@Component({\n  selector: 'mwl-demo-component',\n  changeDetection: ChangeDetectionStrategy.OnPush,\n  templateUrl: 'template.html'\n})\nexport class DemoComponent implements OnInit {\n  view: string = 'month';\n\n  viewDate: Date = new Date();\n\n  recurringEvents: RecurringEvent[] = [\n    {\n      title: 'Recurs on the 5th of each month',\n      color: colors.yellow,\n      rrule: {\n        freq: RRule.MONTHLY,\n        bymonthday: 5\n      }\n    },\n    {\n      title: 'Recurs yearly on the 10th of the current month',\n      color: colors.blue,\n      rrule: {\n        freq: RRule.YEARLY,\n        bymonth: getMonth(new Date()) + 1,\n        bymonthday: 10\n      }\n    },\n    {\n      title: 'Recurs weekly on mondays',\n      color: colors.red,\n      rrule: {\n        freq: RRule.WEEKLY,\n        byweekday: [RRule.MO]\n      }\n    }\n  ];\n\n  calendarEvents: CalendarEvent[] = [];\n\n  ngOnInit(): void {\n    this.updateCalendarEvents();\n  }\n\n  updateCalendarEvents(): void {\n    this.calendarEvents = [];\n\n    const startOfPeriod: any = {\n      month: startOfMonth,\n      week: startOfWeek,\n      day: startOfDay\n    };\n\n    const endOfPeriod: any = {\n      month: endOfMonth,\n      week: endOfWeek,\n      day: endOfDay\n    };\n\n    this.recurringEvents.forEach(event => {\n      const rule: RRule = new RRule(\n        Object.assign({}, event.rrule, {\n          dtstart: startOfPeriod[this.view](this.viewDate),\n          until: endOfPeriod[this.view](this.viewDate)\n        })\n      );\n\n      rule.all().forEach(date => {\n        this.calendarEvents.push(\n          Object.assign({}, event, {\n            start: new Date(date)\n          })\n        );\n      });\n    });\n  }\n}\n"

